body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
*{
  margin: 0px;
  padding: 0px;
}
.Header{
  background: black;
  color: white;
  font-weight: bold;
  overflow: hidden;
}
.header{
  float: left;
}
.header ul{
  display: flex;
  flex-wrap: wrap;
}
.header ul li{
 list-style: none;
 margin: 10px;
}
.header ul a{
  text-decoration: none;
  color: white;
}
.header1 ul{
  display: flex;
  flex-wrap: wrap;
  justify-content: right;
}
.header1 ul li{
  list-style: none;
  margin: 10px;
}
.header1 ul a{
  text-decoration: none;
  color: aqua;
}

.footer{
  background-color: black;
  color: white;
  position: fixed;
  bottom: 0%;
  width: 100%;
  font-weight: bold;
}
.footer ul{
  display: flex;
  justify-content: right;
}
.footer ul li{
  list-style: none;
  margin: 10px;
}
.footer a{
  text-decoration: none;
  color: white;
}
p{
  float: left;
  margin: 10px;
}
.MainBlock{
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
}
.preview_page{
    height: 500px;
